.button{cursor:pointer;display:flex;justify-content:center;align-items:center;min-width:100px;height:3em;color:#aaa;background-color:white;border:3px solid #aaa;border-radius:5px;padding:0.5em;margin:0 20px}.button:hover{color:white;background-color:#aaa}.button:active{background-color:#888}.button__name{margin-left:5px}.button--lightblue{color:dodgerblue;background-color:white;border:3px solid dodgerblue}.button--lightblue:hover{color:white;background-color:dodgerblue}.button--lightblue:active{background-color:#1d7ddd}.button--lightblue--lightgreen{color:forestgreen;background-color:white;border:3px solid forestgreen}.button--lightblue--lightgreen:hover{color:white;background-color:forestgreen}.button--lightblue--lightgreen:active{background-color:#1e7a1e}.dialog{position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center;width:100%;height:100%;background-color:rgba(0,0,0,0.3)}.dialog__title{width:100%;text-align:center}.dialog__input-row{width:100%;margin-bottom:10px}.dialog__input-row__sub-row{width:100%}.dialog__input-row__sub-row .react-datepicker-wrapper{width:100%}.dialog__input-row__sub-row .react-datepicker-wrapper .react-datepicker__input-container{width:100%}.dialog__item-dialog{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;align-content:center;width:300px;background-color:white;border-radius:10px;padding:20px}.dialog__item-dialog input,.dialog__item-dialog select{width:100%}.dialog .button{margin:10px}
