.header{display:flex;justify-content:space-between;align-items:center;height:50px;color:white;background-color:#4502b1;padding:0 20px}.header h1{margin:0}.header__menu{display:flex;align-items:center;height:inherit}.header__menu ul{display:flex;height:inherit;padding:0;margin:0}.header__menu ul li{cursor:pointer;display:flex;justify-content:center;align-items:center;min-width:100px;height:inherit;margin:0 0 0 10px;list-style-type:none}.header__menu ul li:nth-child(1){margin:0}.header__menu ul li:hover{background-color:#00ffff8c}.header__mobile-menu-button{display:none}@media all and (max-width: 576px){.header__menu ul li{min-width:50px}.header__mobile-menu-button{display:block}}
