.summary{display:flex;flex-wrap:wrap;min-width:280px;min-height:180px;width:calc(100% / 3 - 20px);text-align:center;border:1px solid #ddd;margin-bottom:20px;border-radius:5px 5px 0 0}.summary-title{display:flex;justify-content:center;align-items:center;width:100%;height:50px;color:white;background-color:#4502b1;font-size:1.8em;border-radius:5px 5px 0 0}.summary-content{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;align-content:flex-start;width:100%;height:calc(100% - 50px)}.summary-income-block,.summary-charge-block,.summary-balance-block{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;align-content:center;height:50%}.summary-income-block div,.summary-charge-block div,.summary-balance-block div{width:100%;color:#00c800}.summary-income-block div.charge,.summary-charge-block div.charge,.summary-balance-block div.charge{color:red}.summary-income-block div.balance--negative,.summary-charge-block div.balance--negative,.summary-balance-block div.balance--negative{color:red}.summary-income-block,.summary-charge-block{width:50%;border:1px solid #ddd;border-bottom:none}.summary-income-block{border-left:none;border-right:none}.summary-charge-block{border-right:none}.summary-balance-block{width:100%;border:1px solid #ddd;border-left:none;border-right:none;border-bottom:none}@media all and (max-width: 1080px){.summary{width:calc(50% - 20px)}}@media all and (max-width: 820px){.summary{width:100%}}
