.ledger-filter{display:flex;flex-wrap:wrap;justify-content:flex-end;align-items:center;align-content:center;width:100%;height:80px;border-bottom:1px solid #ddd;padding-bottom:20px}.ledger-table{width:100%;height:calc(100% - 80px);overflow:auto;padding-top:20px}.ledger-table table{width:100%;border:1px solid #ddd;border-collapse:collapse}.ledger-table table th,.ledger-table table td{border:1px solid #ddd;padding:5px 10px}.ledger-table table tr:nth-child(even){background-color:#eee}.ledger-table__date-header{width:80px}.ledger-table__money-header{width:100px}.ledger-table__type-header{width:100px}.ledger-table__date-data,.ledger-table__type-data{text-align:center}.ledger-table__money-data{text-align:right}.ledger-table__label{display:inline-block;border-radius:5px;padding:5px}.ledger-table__label.income{color:white;background-color:#4502b1}.ledger-table__label.food{color:white;background-color:pink}.ledger-table__label.clothes{color:white;background-color:orange}.ledger-table__label.residence{color:white;background-color:#caca00}.ledger-table__label.traffic{color:white;background-color:#6baf6b}.ledger-table__label.entertainments{color:white;background-color:dodgerblue}@media all and (max-width: 1280px){.ledger-filter{justify-content:center;height:180px}.ledger-table{height:calc(100% - 180px)}}
