.sidebar{position:fixed;background-color:#eee;width:200px;height:calc(100vh - 50px);padding:20px;z-index:100}.sidebar ul{padding:0}.sidebar ul a{display:block;margin:10px 0}.sidebar__item:hover{color:white}.sidebar__item li{cursor:pointer;list-style-type:none;padding:10px;border-radius:5px}.sidebar__item li:hover{background-color:#4502b1}.sidebar__item--selected{color:white}.sidebar__item--selected li{background-color:#4502b1}@media all and (max-width: 576px){.sidebar{display:none}.sidebar.show{display:block}}
